<template>
    <div class="filter">
        <div class="store-title flex-row-between">
            <CustomRadio :checked="allCheck" @click="toggleAll">
                <span class="store-title__left">ALL</span>
            </CustomRadio>
            <span class="store-title__right pointer" @click="removeAll">Clear Filter</span>
        </div>
        <div class="store-filter" v-for="(group, index) in filter" :key="index">
            <div class="store-filter__title text-left">{{group.title}}</div>
            <el-row>
                <el-col :xs="getMd" :sm="getMd" :md="getMd" :lg="getXl" :xl="getXl" v-for="item in group.subs" :key="item.id">
                    <CustomRadio class="store-filter__radio" :checked="isCheck(item.id, group.checks)" @click="toggleCheck(item, group.checks)">
                        <div class="store-filter__item flex-row-start">
                            <img :src="item.icon" class="store-filter__icon" :style="{width: group.width}"/>
                            <span class="store-filter__value">{{item.name}}</span>
                        </div>
                    </CustomRadio>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>

import CustomRadio from './radio.vue'
export default {
    components: {
        CustomRadio
    },
    props: {
        size: String,
        filter: {
            type: Array,
            default() {
                return []
            }
        }
    },
    computed: {
        getXl() {
            if (this.size === 'small') {
                return 12
            } else if (this.size === 'large') {
                return 6
            }
        },
        getMd() {
            if (this.size === 'small') {
                return 24
            } else if (this.size === 'large') {
                return 12
            }
        },
        allCheck() {
            let state = true
            this.filter.forEach(item => {
                if (item.checks.length != item.subs.length) {
                    state = false
                }
            })
            return state
        }
    },
    methods: {
        isCheck(id, list) {
            return list.includes(id)
        },
        /**
         * 切换当前选中
         */
        toggleCheck(item, list) {
            const index = list.findIndex(id => id === item.id);
            if (index >= 0) {
                list.splice(index, 1)
            } else {
                list.push(item.id)
            }
        },
        // 选中所有
        toggleAll() {
            if (this.allCheck) {
                this.filter.forEach(item => {
                    item.checks = []
                })
            } else {
                this.filter.forEach(item => {
                    item.checks = []
                    item.subs.forEach(sub => {
                        item.checks.push(sub.id)
                    })
                })
            }
        },
        // 移除所有筛选条件
        removeAll() {
            this.filter.forEach(item => {
                item.checks = []
            })
        }
    }
}
</script>

<style lang="scss" scoped>
    .filter{
        padding: .2rem;
        .store-title{
            margin-bottom: .34rem;
        }
        .store-title__left{
            font-size: .24rem;
            color: #fff;
        }
        .store-title__right{
            color: #FCBF2F;
            font-size: .18rem;
        }
        .store-filter{
            margin-bottom: .2rem;
        }
        .store-filter__title{
            color: #888;
            font-size: .2rem;
            margin-bottom: .2rem;
        }
        .store-filter__radio{
            margin-bottom: .2rem;
        }
        .store-filter__item{
            align-items: center;
        }
        .store-filter__icon{
            width: .22rem;
            height: auto;
            margin-right: .08rem;
        }
        .store-filter__value{
            font-size: .18rem;
            color: #fff;
        }
    }
</style>